function shuffleArray(array) {
    let i = array.length - 1;
    for (; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1));
        const temp = array[i];
        array[i] = array[j];
        array[j] = temp;
    }
    return array;
}

function random(num, maxm) {
    let numArray = [];

    for(var i=0; i<maxm; i++)
        numArray.push(i);

    numArray = shuffleArray(numArray);

    return numArray.splice(0, num);
}

export function getQuestions(topics, noQues) {
    const questions = [];

    if (topics === "naruto") {
        const len = naruto.length;
        const ind = random(noQues, len);

        for(var i=0; i<noQues; i++)
            questions.push(naruto[ind[i]]);
    }

    else if (topics === "aot") {
        const len = aot.length;
        const ind = random(noQues, len);

        for(i=0; i<noQues; i++)
            questions.push(aot[ind[i]]);
    }

    else if (topics === "mha") {
        const len = mha.length;
        const ind = random(noQues, len);

        for(i=0; i<noQues; i++)
            questions.push(mha[ind[i]]);
    }

    else if (topics === "ptm") {
        const len = ptm.length;
        const ind = random(noQues, len);

        for(i=0; i<noQues; i++)
            questions.push(ptm[ind[i]]);
    }

    return questions;
}